.width {
  &__L {
    max-width: 136rem;
  }

  &__M {
    max-width: 100rem;
  }

  &__full {
    width: 100%;
  }
}

.contrast {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 50%;
    bottom: -5rem;
    width: 100vw;
    background-color: $gray4;
    transform: translateX(-50%);
  }
}

.pt-0 {
  padding-top: 0;
}

.pt-3 {
  padding-top: 3rem;
}

