.heading {
  color: $purple;
  font-size: $fz-xl;
  line-height: 5rem;
  text-transform: uppercase;

  .App--oil & {
    color: $red3;
  }

  &.small {
    font-size: $fz-m;
    line-height: 2.4rem;
    color: $black;
    text-transform: none;
  }

  &__sub {
    padding: 1rem 0 0;
    font-size: $fz-xs;
  }
}

@media (max-width: 1680px) {
  .heading {
    .pieChartProduction + & {
      grid-column: span 2;
    }
  }
}

@media (max-width: 1023px) {
  .heading {
    font-size: 2.5rem;
  }
}

@media (max-width: 767px) {
  .heading {
    width: calc(100vw - 5rem);
    grid-column: 1;
    font-size: $fz-m;

    .pieChartProduction + & {
      width: calc(100vw - 5rem);
      grid-column: 1;
    }

    &.small {
      > * {
        line-height: 1.25;
        font-weight: 400;
      }
    }
  }
}