.subheader {
  position: fixed;
  z-index: 50;
  top: 19rem;
  left: 0;
  right: 0;
  max-width: 170rem;
  padding: 4rem 4rem 20rem;
  margin: 0 auto;
  pointer-events: none;
  opacity: 0;
  transition: opacity $eia200;
  backdrop-filter: blur(5rem);
  background-color: rgba($white, .8);
  box-shadow: 0 0 1rem 0 $gray;
  border-radius: 0 0 1rem 1rem;

  &.visible {
    opacity: 1;
    pointer-events: all;
  }

  &__wrapper {
    max-width: 180rem;
    margin: 0 auto;
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 0 10rem;
    max-width: 144rem;
    padding: 0 0 0 10%;
    margin: 0 auto;
  }

  &__value-wrapper {
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 4rem;

    &--short {
      grid-column: span 1;
      margin: 0 0 6rem;
    }
  }

  &__value-box {
    width: 100%;

    .subheader__value-wrapper--short & {
      margin: 0 0 .6rem;
      border-bottom: .1rem solid currentColor;
    }
  }

  &__value,
  &__value-tab {
    font-size: 2rem;
    font-weight: 700;
    transition: color $eia200;

    &:hover,
    &.active {
      color: $purple;

      .App--oil & {
        color: $red3;
      }
    }
  }

  &__value {
    .subheader__value-wrapper--short & {
      margin: 0 0 .6rem;
    }
  }

  &__value-tab {
    margin: 1.2rem 0;
    font-size: $fz-m;
    font-weight: 400;

    &--notTab {
      font-weight: 700;
    }
  }
}

@media (max-width: 1680px) {
  .subheader {
    top: 15rem;
  }
}

@media (max-width: 1280px) {
  .subheader {
    padding: 1rem 2rem;

    &__value {
      font-size: 1.4rem;
    }
  }
}

@media (max-width: 1023px) {
  .subheader {
    &__desc {
      margin: 0 1rem 0 0;
      font-size: 1.5rem;
    }

    &__value {
      font-size: 1.2rem;
    }

    &__toggle {
      margin: 0 0 0 1rem;
    }
  }
}

@media (max-width: 767px) {
  .subheader {
    display: none;
  }
}