.lineChart {
  display: grid;
  grid-template-columns: 80rem 1fr;
  height: 50rem;

  &__desc {
    padding: 6rem 0 0 7rem;
  }
}

@media (max-width: 1280px) {
  .lineChart {
    grid-template-columns: 1fr 18rem;
  }
}

@media (max-width: 1023px) {
  .lineChart {
    grid-template-columns: 1fr;
    height: auto;
  }
}