.page {
  position: relative;

  &__section {
    display: grid;
    grid-template-columns: 90rem 1fr;
    @include contentWidth;
    margin-bottom: 5rem;
    align-items: baseline;
    justify-items: flex-end;
    padding: 9rem 0 0;
    border-top: .1rem solid $purple;

    .App--oil & {
      border-color: $red3;
    }

    &--assets {
      padding: 25rem 0 0;
    }
  }

  &__sectionTitle {
    @include contentWidth;
    min-height: 5.5rem;
    margin: 0 auto 6.5rem;
    font-size: $fz-xxxl;
    line-height: 7.5rem;
    color: $black;
    text-transform: uppercase;
    letter-spacing: 1.03rem;
    transition: font-size $eia200;

    .page__section & {
      margin: 0;
      justify-self: flex-start;

      &--supply {
        margin: 0 0 12rem;
      }
    }

    .page__section--assets & {
      grid-column: span 2;
      max-width: 120rem;
    }

    &.active {
      position: sticky;
      top: 12.5rem;
      left: 0;
      z-index: 15;
      font-size: 3.5rem;
      line-height: 1;
    }
  }

  &__stickyNav {
    &.active {
      position: sticky;
      top: 13.6rem;
      left: 0;
      z-index: 15;
    }
  }

  &__tabsContent {
    grid-column: span 2;
  }

  &__wide {
    grid-column: span 2;
  }

  &__innerTabs-content {
    position: relative;
  }

  &__innerTitle {
    padding: 15rem 0 0;
    margin: 0 0 2rem;
  }

  &__innerTabs {
    margin: 1rem 0 1.5rem 13rem;
  }

  &__tabContent {
    max-width: 120rem;
    width: 100%;
  }

  &__lineChart {
    padding: 4rem 0 0 12.5rem;
    margin: 0 0 4rem;
  }

  &__partsContent {
    margin: 3rem 0;

    &--prices {
      max-width: 130rem;
    }
  }

  &__productionParts {
    width: 100rem;
    padding: 6rem 0 0;
    margin: 0 0 7rem;
  }

  &__pieChartProduction {
    width: 100%;
    margin: 0 0 9rem;
  }

  &__leftItem {
    justify-self: flex-start;
  }

  &__leftPadding {
    padding-left: 13rem;
  }

  &__outputList {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10rem 15rem;
    width: 100%;
    padding: 10rem 0 0 13rem;
    margin: 0 0 10rem;
  }

  &__mapLarge {
    max-width: 180rem;
  }
}

@media (max-width: 1680px) {
  .page {
    &__innerTabs {
      margin: 0 0 1.5rem;
    }

    &__innerTitle {
      padding: 6rem 0 0;
    }

    &__section {
      &--mining,
      &--production,
      &--supply {
        grid-template-columns: 60rem 1fr;
        padding: 6rem 0 0;
      }

      &--assets {
        padding: 16rem 0 0;
      }
    }

    &__sectionTitle {
      margin: 0 auto 6.5rem;
      line-height: 1;

      .page__section & {
        margin: 0 0 5rem;
      }

      &.active {
        top: 9rem;
      }
    }

    &__leftPadding {
      padding-left: 0;
    }

    &__stickyNav {
      &.active {
        top: 10.5rem;
      }
    }

    &__outputList {
      grid-template-columns: repeat(3, 1fr);
      justify-self: self-start;
      grid-gap: 7.5rem 13rem;
      max-width: 105rem;
      padding: 4rem 0 0;
    }

    &__assetsMap {
      width: 105rem;
    }

    &__mapLarge {
      max-width: 120rem;
    }
  }
}

@media (max-width: 1280px) {
  .page {
    &__section {
      max-width: 98rem;

      &--mining,
      &--production,
      &--supply,
      &--prices {
        grid-template-columns: 50rem 1fr;
      }
    }

    &__productionParts {
      width: auto;
    }

    &__assetsMap {
      width: 90rem;
    }

    &__mapLarge {
      max-width: 100rem;
    }
  }
}

@media (max-width: 1023px) {
  .page {
    &__section {
      grid-template-columns: 1fr;
      padding: 9rem 2rem 0;

      &--mining,
      &--production,
      &--supply,
      &--prices {
        grid-template-columns: 1fr;
      }

      &--assets {
        padding: 0;
        margin: 0;
      }
    }

    &__sectionTitle {
      font-size: 4rem;

      &.active {
        top: 9.5rem;
        font-size: 4rem;
      }
    }

    &__outputList {
      grid-gap: 3rem 3rem;
    }

    &__assetsMap {
      display: none;
    }
  }
}

@media (max-width: 767px) {
  .page {
    margin: 0 0 3rem;

    &__section {
      min-height: 0;
      min-width: 0;
      width: calc(100vw - 5rem);
      grid-template-columns: 1fr;
      padding: 8rem 0 0;
      margin: 0 2.5rem;

      &--field {
        border: none;
      }

      &--mining,
      &--production,
      &--supply {
        grid-template-columns: 1fr;
      }

      &--assets {
        padding: 0;
        margin: 0 2.5rem;
      }
    }

    &__sectionTitle {
      padding: 0 0 4rem 2.5rem;
      margin: 0 auto 2rem;
      font-size: 2rem;
      line-height: 1;
      letter-spacing: normal;

      &.active {
        position: relative;
        top: 0;
        font-size: 2rem;
      }

      .page__section & {
        min-height: auto;
        padding: 0;
        margin: 0 0 4rem;
      }
    }

    &__innerTitle {
      padding: 1rem 0 0;
      margin: 0 0 3.5rem;
    }

    &__innerTabs {
      margin: 0 0 3.5rem;
    }

    &__wide {
      grid-column: 1;
    }

    &__stickyNav {
      overflow-x: scroll;
      display: flex;
      width: 100%;
      grid-column-start: 1;
      align-items: center;
      padding: 0 0 1.5rem;
      margin: 0;

      &.active {
        position: relative;
        top: 0;
      }
    }

    &__leftItem {
      grid-column: 1;
    }

    &__outputList {
      width: calc(100vw - 5rem);
      grid-template-columns: 1fr 1fr;
      grid-gap: 1rem 1.5rem;
      margin: 0 0 5rem;
    }

    &__productionParts {
      width: calc(100vw - 5rem);
      min-width: 0;
      min-height: 0;
      padding: 3rem 0 0;
      margin: 0 0 5rem;
    }

    &__partsContent {
      margin: 1rem 0;
    }

    &__pieChartProduction {
      margin: 0 0 6rem;
    }
  }
}