.lineChartSole {
  display: grid;
  grid-template-columns: 80rem 1fr;

  &__desc {
    padding: 2rem 0 0 5rem;
  }
}

@media (max-width: 1280px) {
  .lineChartSole {
    &__desc {
      padding: 2rem 0 0 2rem;
    }
  }
}

@media (max-width: 1023px) {
  .lineChartSole {
    grid-template-columns: 1fr;
    width: 100%;
    max-width: 80rem;

    &__desc {
      grid-row-start: 1;
      padding: 0;
    }
  }
}

@media (max-width: 767px) {
  .lineChartSole {
    margin: 0 -2.5rem;

    &__chart-box {
      width: 100vw;
      height: 20rem;
    }

    &__desc {
      padding: 0 2.5rem;
    }
  }
}