.mapTransport {
  position: relative;
  min-height: 107rem;

  &__title {
    position: relative;
    z-index: 1;
    padding: 0 0 0 13rem;
  }

  &__map {
    position: absolute;
    top: 6rem;
    left: -12rem;
  }

  &__mapPart {
    position: absolute;
    z-index: 1;
    opacity: 0;
    transition: opacity $eia250;

    &--big {
      top: 23rem;
      left: 6rem;
      width: 142.7rem;
      height: 58.8rem;
    }

    &--small {
      top: 28.6rem;
      left: 62.6rem;
      width: 86.2rem;
      height: 46.5rem;
    }

    &--percentage {
      bottom: 10rem;
      width: 192rem;
      left: -12rem;
      opacity: 1;
    }

    &.active {
      opacity: 1;
    }
  }

  &__toggle {
    position: absolute;
    z-index: 2;
    top: 38rem;
    left: 101.5rem;
    width: 8.5rem;
    height: 25rem;
    background-color: transparent;
  }
}

@media (max-width: 1680px) {
  .mapTransport {
    min-height: 80rem;

    &__map {
      width: 144rem;
      top: 0;
    }

    &__mapPart {
      &--big {
        top: 5.6rem;
        left: 0.5rem;
        width: 110rem;
      }

      &--small {
        top: 11.6rem;
        width: 58.2rem;
        left: 48.4rem;
      }

      &--percentage {
        bottom: 11.5rem;
        width: 144rem;
      }
    }

    &__toggle {
      top: 24rem;
      left: 74rem;
      width: 7rem;
      height: 20rem;
    }
  }
}

@media (max-width: 1280px) {
  .mapTransport {
    min-height: 60rem;

    &__map {
      left: -3rem;
      width: 106rem;
    }

    &__mapPart {
      &--big {
        top: -3.4rem;
        left: 7.5rem;
        width: 80rem;
      }

      &--small {
        top: 2.5rem;
        width: 48rem;
        left: 39.4rem;
      }

      &--percentage {
        bottom: 11.5rem;
        width: 106rem;
      }
    }

    &__toggle {
      top: 18rem;
      left: 61rem;
      width: 5rem;
      height: 14rem;
    }
  }
}

@media (max-width: 1023px) {
  .mapTransport {
    display: none;
  }
}