.footer {
  background-color: $gray4;

  &__content {
    display: grid;
    grid-template-columns: 25rem 1fr;
    grid-gap: 6rem 16rem;
    padding: 4rem 0;
    width: 100%;
    max-width: 136rem;
    margin: 0 auto;
  }

  &__wide {
    grid-column: span 2;
  }

  &__title {
    font-size: 3.6rem;
    line-height: 5.3rem;
  }

  &__subtitle {
    font-size: $fz-s;
  }

  &__nav-box {
    display: grid;
    align-items: center;
    grid-template-columns: auto 1fr;
    grid-gap: 1.5rem 0;
    padding: 2rem 0 0;
  }

  &__nav {
    display: flex;
    align-items: center;
  }

  &__nav-title {
    font-size: $fz-s;
    font-weight: 700;
  }

  &__nav-item {
    font-size: $fz-s;
    line-height: 2rem;
    margin: 0 0 0 1.5rem;
  }

  &__nav-value {
    transition: color $eia200;

    &.active,
    &:hover {
      color: $purple;

      .App--oil & {
        color: $red3;
      }
    }
  }

  &__poweredBy {
    width: 17rem;
    left: 3.7rem;
    fill: $gray2;
  }
}

@media (max-width: 1680px) {
  .footer {
    &__content {
      max-width: 120rem;
    }
  }
}

@media (max-width: 1280px) {
  .footer {
    &__content {
      max-width: 98rem;
      grid-gap: 6rem 10rem;
    }
  }
}

@media (max-width: 1023px) {
  .footer {
    &__content {
      padding: 4rem 2rem;
    }

    &__nav-box {
      display: none;
    }

    &__wide {
      justify-self: flex-end;
      align-self: flex-end;
      grid-column: auto;
    }
  }
}

@media (max-width: 767px) {
  .footer {
    width: 100vw;
    padding: 1.5rem 2.5rem;

    &__content {
      padding: 0;
    }

    &__title-box,
    &__nav-box {
      display: none;
    }
  }
}