.structureOverview {
  padding: 0 0 0 25rem;
  margin: 0 0 4rem;

  &__upper {
    @include contentWidth;
    display: grid;
    grid-template: 1fr 1fr/ fit-content(10rem) 1fr;
    grid-gap: 3rem 2rem;
    align-items: baseline;
    padding: 0 0 0 1rem;
  }

  &__amount {
    font-size: $fz-xxxxl;
    letter-spacing: .083rem;
    color: $purple;

    .App--oil & {
      color: $red3;
    }
  }

  &__value {
    font-size: $fz-xl;
    line-height: 4rem;
    color: $black;
    text-transform: uppercase;
    font-weight: 400;
  }

  &__desc {
    font-size: $fz-s;
    line-height: 2rem;
    grid-column: 2/3;
  }
}

@media (max-width: 1680px) {
  .structureOverview {
    padding: 0;
  }
}

@media (max-width: 767px) {
  .structureOverview {
    min-height: 0;
    min-width: 0;
    width: calc(100vw - 5rem);

    &__upper {
      grid-gap: 1rem 1rem;
      padding: 0;
      margin: 0 0 4rem;
    }
  }

  &__amount {
    font-size: 2.5rem;
  }

  &__value {
    font-size: 1.4rem;
    text-transform: none;
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 1.25;
    grid-column: 1/3;
  }
}