.complexTable {
  &__row {
    display: grid;
    grid-template-columns: 1fr repeat(4, 17%);
    align-items: center;
    font-size: $fz-s;


    &--header {
      border-bottom: .1rem solid $blue2;

      .App--oil & {
        border-color: $red3;
      }
    }

    &--large {
      font-size: $fz-m;
    }

    &--top {
      border-top: .1rem solid $gray7;
    }

    &--bottom {
      border-bottom: .1rem solid $gray7;
    }
  }

  &__cellWrapper {
    display: flex;
    align-items: flex-start;

    .complexTable__row--header & {
      border-top: .1rem solid $gray7;
    }
  }

  &__cell {
    display: flex;
    align-items: center;
    height: 100%;
    padding: .8rem;
    line-height: 3rem;

    &:not(:empty) {
      width: 100%;
      text-align: center;
      border-left: .1rem solid $gray7;
    }

    &:first-of-type {
      border-left: none;
    }

    .complexTable__cellWrapper & {
      width: 50%;
      justify-content: flex-end;

      &:first-of-type {
        border-left: .1rem solid $gray7;
      }
    }

    .complexTable__row--header & {
      justify-content: center;
    }

    &--red {
      color: $red2;
    }

    &--green {
      color: $green;
    }
  }
}