.OilExportTransportMap {
  position: relative;
  min-height: 107rem;

  &__title {
    position: relative;
    z-index: 1;
    padding: 0 0 0 13rem;
  }

  &__map {
    position: absolute;
    top: 0;
    left: -15rem;
  }

  &__mapPart {
    position: absolute;
    z-index: 1;
    opacity: 0;
    transition: opacity $eia250;

    &--big {
      top: 10rem;
      left: 0;
      width: 162rem;
      height: 81rem;
    }

    &--small {
      top: 17.8rem;
      left: -36.9rem;
      width: 220.4rem;
      height: 55.4rem;
    }

    &--percentage {
      bottom: 18rem;
      left: 43rem;
      width: 76rem;
      opacity: 1;
    }

    &.active {
      opacity: 1;
    }
  }

  &__toggle {
    position: absolute;
    z-index: 2;
    top: 24rem;
    left: 114.5rem;
    width: 9.5rem;
    height: 30rem;
    background-color: transparent;
  }
}

@media (max-width: 1680px) {
  .OilExportTransportMap {
    min-height: 80rem;

    &__map {
      width: 144rem;
      top: 0;
    }

    &__mapPart {
      &--big {
        top: -0.4rem;
        left: 0.5rem;
        width: 110rem;
      }

      &--small {
        top: 8.4rem;
        left: -1.7rem;
        width: 102rem;
      }

      &--percentage {
        bottom: 12.5rem;
        left: 28rem;
        width: 54rem;
      }
    }

    &__toggle {
      top: 19rem;
      left: 83rem;
      width: 7rem;
      height: 23rem;
    }
  }
}

@media (max-width: 1280px) {
  .OilExportTransportMap {
    min-height: 60rem;

    &__map {
      left: -3rem;
      width: 106rem;
    }

    &__mapPart {
      &--big {
        top: -3.4rem;
        left: 7.5rem;
        width: 80rem;
      }

      &--small {
        top: 2.5rem;
        width: 48rem;
        left: 39.4rem;
      }

      &--percentage {
        bottom: 11.5rem;
        width: 106rem;
      }
    }

    &__toggle {
      top: 18rem;
      left: 61rem;
      width: 5rem;
      height: 14rem;
    }
  }
}

@media (max-width: 1023px) {
  .OilExportTransportMap {
    display: none;
  }
}